import { VaultOpenProps } from "asset/constant/interface";
import { iconMinus, iconPlus } from "asset/exportImages";
import { vaultDashboardData } from "data/vaultDashboard";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";

const Vault = () => {
  const route = useNavigate();

  const [isOpenStatus, setIsOpenStatus] = useState<VaultOpenProps[]>([
    {
      name: "Profile",
      status: false,
    },
    {
      name: "Education",
      status: false,
    },
    {
      name: "Skill",
      status: false,
    },
    {
      name: "Work Experience",
      status: false,
    },
    {
      name: "Project",
      status: false,
    },
    {
      name: "Certificate",
      status: false,
    },
    {
      name: "Award",
      status: false,
    },
    {
      name: "Thesis, Patent, Copyright",
      status: false,
    },
    {
      name: "Seminar, Academy",
      status: false,
    },
    {
      name: "Foreign Language",
      status: false,
    },
    {
      name: "External Activity",
      status: false,
    },
    {
      name: "Military",
      status: false,
    },
  ]);

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);

  return (
    <div className="main">
      <div className="header-cover">
        <div className="header">
          <p className="brand">Alec J Portfolio</p>

          <div className="center">
            <p onClick={() => route("/")}>Home</p>
            <p onClick={() => route("/timeline")}>Timeline</p>
          </div>

          <p className="right active">Vault</p>
        </div>
      </div>

      <p className="motto">데이터는 항상 더 나은 아이디어를 가진다</p>
      <p className="motto-eng">Data Always Has A Better Idea</p>

      <div className="dashboard-container">
        {vaultDashboardData.map((item) => (
          <div key={item.name} className="each-box-cover">
            <div className="each-box">
              <p className="title">{item.name}</p>
              <p className="year">{item.year}</p>
            </div>
          </div>
        ))}
      </div>

      <div className="content-container">
        {isOpenStatus.map((item) => (
          <div
            key={item.name}
            className={`each-block ${
              isOpenStatus.find((items) => items.name === item.name).status &&
              "active"
            }`}
          >
            <div
              className="top-line"
              onClick={() =>
                setIsOpenStatus(
                  isOpenStatus.map((items) =>
                    item.name === items.name
                      ? { ...items, status: !item.status }
                      : items
                  )
                )
              }
            >
              <p className="title">{item.name}</p>
              <img
                src={item.status ? iconMinus : iconPlus}
                width="16"
                height="16"
                alt=""
              />
            </div>

            {item.status && (
              <div className="content">
                <p>테스트</p>
              </div>
            )}
          </div>
        ))}

        <p></p>
      </div>

      <p className="vault-copyright">
        © 2023 Alec J Portfolio. All rights are reserved.
      </p>
    </div>
  );
};

export default Vault;
