import moment from "moment";

export const checkDiffYear = (date: string) => {
  const today = moment().unix();
  const compare = moment(date).unix();
  const diff = (today - compare) / 31536000;
  console.log((today - compare) / 2592000);

  if (diff.toFixed(1).split(".")[1] !== "0") {
    const diffForMonth = (today - compare) / 2592000;

    return `${diffForMonth.toFixed(0)}개월`;
  }

  return `${diff.toFixed(0)}년`;
};
